.modalWrapper {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.9);
    position: absolute;
}
.modalContent {
    position: absolute;
    width: 500px;
    height: 500px;
    background-color: white;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    gap: 15px;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}
